import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AuthGuard } from 'src/app/core/auth/guards/auth.guard';

import { AssetsReportsComponent } from './assets-reports.component';
import { AssetsReportDataComponent } from './assets-report-data/assets-report-data.component';

const routes: Routes = [
  { path: '', component: AssetsReportsComponent, canActivate: [AuthGuard]},
	{ path: ':name/:id', component: AssetsReportDataComponent, canActivate: [AuthGuard]},
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AssetsReportsRoutingModule { }
