@Component({
  selector: 'app-assets-reports',
  templateUrl: './assets-reports.component.html',
  styleUrls: ['./assets-reports.component.scss']
})
export class AssetsReportsComponent implements OnInit {


    pageName: string = "";
    pageDesp: string = "";

    userReportDataList: any;
  
    userReportData: any;
  
    ItemStartIndex: any = 0;
    ItemEndIndex: any;
    totalItems: any;
    itemLimit: any = 10;
  
    unitFieldType: string = "unitno";
    unitOrder: boolean = true;
  
    isDataLoaded: boolean = false;
  
    unitBlocksData: any;
    blockId: string = "";
  
    blockNo: string = "All Blocks";
    reportDataList:any =[]


    inventoryHeader:any
    inventoryHistoryData:any;
    gridSourceData:any;
    warehouseData:any = [];
    searchWareHouseId:any='';
  
    @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;
  
  
    constructor(
      private router: Router,
      private route: ActivatedRoute,
      private userService: UserService,
      private apartmentService: ApartmentService,
      private lookupService: LookupService,
      private sharedService: SharedService,
      private sessionService: SessionService,
      private purchaseOrderService:PurchaseOrderService
    ) { }
  
    getIndexParams(event) {
      this.ItemStartIndex = event.ItemStartIndex;
      this.ItemEndIndex = event.ItemEndIndex;
      // this.itemLimit = event.itemLimit;
    }
  
    sortUnitData(type) {
      this.unitFieldType = type;
      this.unitOrder = !this.unitOrder;
    }
  
    getFieldOrderBy(type) {
      if (this.unitFieldType == type) {
        return this.unitOrder ? 'asc' : 'desc';
      }
      else return '';
    }
  
    isMobileView() {
      return window.innerWidth <= 767 ? 'table-responsive' : '';
    }
    
    onGlSearchFilter() {
      
    }

    getReportData(){
      let details = {
        LookupTypeId: 87, //lookuptypeid for report
        MenuName: 'UnitUser'
      }
  
      this.lookupService.getLookupValuesByApartmentIdLookupTypeIdMenuName(details).subscribe((res:any) => {
  
        this.reportDataList = res;
  
        this.isDataLoaded = true;
  
      })
    }
    showItems() {
  
      this.totalItems = this.userReportDataList.length;
  
      if (this.totalItems > this.itemLimit) {
        this.ItemEndIndex = this.itemLimit;
      }
      else {
        this.ItemEndIndex = this.totalItems;
      }
  
    }
  
  




    
    
  
    ngOnInit() {

      this.pageName = this.route.params['value'].name;
  
      let unitBlockParams = {
        apartmentId: this.sessionService.apartmentId
      }
  
      this.apartmentService.getApartmentBlockByApartmentId(unitBlockParams).subscribe((res: any) => {
        this.unitBlocksData = res;
      });
  
      let params = {
        LookupTypeId: 87,
        ApartmentId: this.sessionService.apartmentId
      }
  
      this.lookupService.getLookupValueByLookupTypeId(params).subscribe((res: any) => {
        var data = res.filter(item => {
          return item.lookupValueId == this.route.params['value'].id
        })
        this.pageName = res[0].lookupValueName;
        this.pageDesp = res[0].description;
      });
  
      // this.getBlockDetails();
      this.getReportData();
  
      var cellsrenderer = (row, column, value) => {
        return '<div class="jqx-custom-inner-cell">' + value + '</div>';
      }
  
      var columnrenderer = (value) => {
        return '<div style="padding: 14px">' + value + '</div>';
      }


      this.inventoryHeader = [{
            text: 'No',
           datafield: 'materialId',
           width: 100,
           cellsrenderer: cellsrenderer,
           renderer: columnrenderer
       },
       {
        text: 'Name',
           datafield: 'materialName',
           minwidth: 100,
           cellsrenderer:cellsrenderer,
           renderer: columnrenderer
       },
        {
        text: ' Desription',
           datafield: 'materialName1',
           minwidth: 100,
           cellsrenderer:cellsrenderer,
           renderer: columnrenderer
       }, {
        text: 'Qty',
           datafield: 'stockQty',
           cellsrenderer: cellsrenderer,
           minwidth: 100,
           renderer: columnrenderer
       }, 
        {
        text: 'Status',
           datafield: 'stockStatus',
           cellsrenderer:cellsrenderer,
           minwidth: 100,
           renderer: columnrenderer
       }];
      this.getCurrentInventory();
    }


    getPrintParams(event){
      this.datagrid.exportdata(event, 'InventoryCurrentDetails', true, null, true, 'https://www.jqwidgets.com/export_server/dataexport.php');
    }
    getCurrentInventory(){
      let params:any = {};
      params.ApartmentId = this.sessionService.apartmentId;
       this.purchaseOrderService.getAllInventoryCurrentCountByApartmentId(params).subscribe((res:any) => {
        if(res) {
        this.gridSourceData = {
          localdata:  res.length > 0 ? res:[],
          datatype: "array"
        }  
        this.inventoryHistoryData = new jqx.dataAdapter(this.gridSourceData);
      }
       });
      
    }

  

    // onSearchFilter(){
    //   if(this.histroySearch != ""){
    //     let filterGroup = new jqx.filter();
    //     let filterOperator = 1;
    //     let filterValue = this.histroySearch;
    //     let filterCondition = 'contains';
    //     let filterData = filterGroup.createfilter('stringfilter', filterValue, filterCondition);
    //     filterGroup.operator = 'or';
    //     filterGroup.addfilter(filterOperator, filterData);
    //     this.datagrid.showfiltercolumnbackground(false);
    //     this.inventoryHeader.forEach(item => {
    //       if(item.datafield != 'Actions') {
    //         this.datagrid.addfilter(item.datafield, filterGroup, true);
    //       }
    //     })
    //     this.datagrid.applyfilters();
    //   }
    //   else {
    //     this.datagrid.clearfilters();
    //   }
    // }
  


  }
  